:root {
    --color-outline: yellow;
}

.App {

}

header.header {
    background-color: #333;
    color: white;
    padding: 1rem;
    margin: 0;
    display: grid;
    grid-template-columns: 2fr 4fr 1fr;
    grid-gap: 1rem;
}

header.header .brand {
    text-transform: uppercase;
    text-shadow: 2px 2px 2px #333;
    line-height: 2em;
    font-weight: bold;
}

header.header .search-container,
header.header .tag-container {
    margin: 2rem 0 1rem;
}

header.header .search-container input {
    width: 50%;
    font-size: 1.2em;
}

header.header .brand a {
    text-decoration: none;
    color: white;
}

header.header .brand:first-letter {
    color: red;
    font-size: 2rem;
}

header.header .tag-container select {
    width: 80%;
}

.content {
    margin: 2rem 0;
    min-height: 100vh;
}

textarea#add-text {
    width: calc(100% - 2rem);
}

button#add-text-btn {
    width: 200px;
    cursor: pointer;
    padding: 1rem;
}

textarea#add-text,
button#add-text-btn {
    margin: 1rem;
    border: none;
    border-bottom: hotpink solid thick;
    border-radius: 4px;
}

button#add-text-btn:hover {
    border-bottom-color: deeppink;
    background-color: grey;
}

button#add-text-btn:active {
    border-bottom-color: red;
    background-color: lightgrey;
}

.stamps {
    margin: 2rem 0;
}

.stamps .ts {
    border-bottom: deeppink dotted 1px;
    font-size: 0.7em;
    display: inline-block;
}

.stamps .note {
    white-space: pre-wrap;
    margin: 1rem 0 3rem;
}

.stamp {
    min-height: 200px;
    margin-bottom: 1rem;
}

.stamp h1 {
    border-bottom: thick solid black;
}

.stamp h2 {
    border-bottom: medium solid black;
}

.stamp h3 {
    border-bottom: thin solid black;
}

.stamp h4 {
    border-bottom: thin dashed black;
}

.stamp h5 {
    border-bottom: thin dotted black;
}

.stamp h6 {
    border-bottom: thin dotted grey;
}

.stamp pre {
    font-family: monospace;
    text-align: left;
}

.stamp .highlighted {
    font-weight: bold;
}

footer {
    background-color: black;
    color: white;
    padding: 2rem;
}

footer a {
    color: white;
    font-weight: bold;
}

footer .controls {
    font-size: .8em;
}

footer h6 {
    margin: 0;
}

.download-control,
.upload-control {
    display: block;
    padding: 1rem 3rem;
    min-width: 100px;
    border: thin solid white;
}

.tags {
    font-size: .8em;
}

.tags .tag + .tag {
    margin: 0 .5rem;
}

.tags .tag:hover {
    outline: thin solid var(--color-outline);
    cursor: pointer;
}

.filter-close {
    padding: 1rem;
    margin: 1rem;
    background-color: lightblue;
    cursor: pointer;
    display: inline-block;
}

.stamp-actions {
    margin: 1.5rem 0;
}

.stamp-actions .btn + .btn {
    margin-left: .5rem;
}

.stamp-actions .btn {
    padding: .4rem 2rem;
    display: inline-block;
    cursor: pointer;
    border: thin solid teal;
    border-radius: 4px;
}

.stamp-actions .btn:hover {
    box-shadow: 2px 2px 2px blue;
}

.stamp-actions .btn:active {
    box-shadow: 2px 2px 2px red;
    transform: scale(1.1, 1.1);
}

.stamp-actions .btn.fav {
    border-bottom-color: hotpink;
}

/* media queries */

@media screen and (min-width: 601px) {

    .stamps {
        margin: 1rem;
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 2rem;
    }

    footer {
        width: 100%;
    }

}

@media screen and (min-width: 1201px) {
    .stamps {
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 4rem 4rem;
    }

    footer {
        display: grid;
        grid-gap: 1rem;
        grid-template-columns: 1fr 1fr 1fr;
    }

    footer .controls {
        display: grid;
        grid-gap: 1rem;
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media screen and (max-width: 600px) {
    header.header {
        display: grid;
        grid-template-columns: 1fr;
    }

    header.header .brand,
    header.header .search-container,
    header.header .tag-container {
        margin: 0;
    }

    textarea#add-text,
    button#add-text-btn {
        display: block;
        width: calc(100% - 2rem);
        margin: 1rem;
    }

    .stamps {
        margin: 1rem;
    }

    footer {
        width: 600px;
    }

    .stamp-actions {
        visibility: visible;
    }
}

@media screen and (max-width: 400px) {
    header.header {
        display: grid;
        grid-template-columns: 1fr;
    }

    header.header .brand,
    header.header .search-container,
    header.header .tag-container {
        margin: 0;
    }

    textarea#add-text,
    button#add-text-btn {
        width: calc(100% - 4rem);
    }

    footer {
        width: 100%;
        grid-template-columns: 1fr;
    }

    footer .controls {
        grid-template-columns: 1fr;
    }

    .stamp-actions {
        visibility: visible;
    }
}

@media screen and (max-width: 300px) {
    header.header {
        display: grid;
        grid-template-columns: 1fr;
    }

    header.header .brand,
    header.header .search-container,
    header.header .tag-container {
        margin: 0;
    }

    textarea#add-text,
    button#add-text-btn {
        width: calc(100% - 4rem);
    }

    footer {
        width: 300px;
        grid-template-columns: 1fr;
    }

    footer .controls {
        grid-template-columns: 1fr;
    }

    .stamp-actions {
        visibility: visible;
    }
}

